import { createClient, SupabaseClient } from "@supabase/supabase-js";

export type ConfessionRow = {
	id: number;
	text: string;
	created_at: string;
};

interface Database {
	public: {
		Tables: {
			confessions: {
				Row: ConfessionRow;
				Insert: {
					text: string;
				};
			};
			safe_confessions: {
				Row: ConfessionRow;
			};
		};
		Functions: {
			get_last_confession_time: {
				Returns: string | null;
			};
			whoami: {
				Returns: {
					is_admin: boolean;
					is_banned: boolean;
				};
			};
		};
	};
}

export const supabase: SupabaseClient<Database> = createClient<Database>(
	"https://lgeitjhzzcaedysmdjou.supabase.co",
	"sb_publishable_Xp5HF0lHHamBOTUe6WXbLw_7RAYlYXy"
);
