import { User } from "@supabase/supabase-js";
import { FormEvent, useEffect, useMemo, useRef, useState } from "react";
import { toast } from "react-toastify";
import ConfessionCard from "./components/ConfessionCard";
import { Spinner } from "./components/Spinner";
import { ConfessionRow, supabase } from "./db";
import "./index.css";

export default function App() {
	const [user, setUser] = useState<User | null>(
		JSON.parse(localStorage.getItem("user") as string)
	);
	const [message, setMessage] = useState("");
	const [confessions, setConfessions] = useState<ConfessionRow[]>([]);
	const [loadingConfs, setLoadingConfs] = useState(false);
	const [submitting, setSubmitting] = useState(false);
	const [secondsLeft, setSecondsLeft] = useState(0);
	const [isAdmin, setIsAdmin] = useState<boolean>(false);
	const [isBanned, setIsBanned] = useState<boolean>(false);
	const [banReason, setBanReason] = useState<string>("");
	const submitDisabled = useMemo<boolean>(
		() =>
			submitting ||
			!message.trim() ||
			message.length < 5 ||
			user === null ||
			secondsLeft > 0 ||
			isBanned,
		[message, user, secondsLeft, isBanned]
	);
	const timerRef = useRef<number>(0);

	useEffect(() => {
		if (window.location.hash.startsWith("#access_token")) {
			const queryString = window.location.hash.replace("#", "?");
			const newUrl =
				window.location.origin + window.location.pathname + queryString;
			window.location.replace(newUrl);
		}

		supabase.auth.getUser().then(({ data: { user } }) => {
			if (user !== null)
				localStorage.setItem("user", JSON.stringify(user));
			else localStorage.removeItem("user");

			setUser(user);
		});
	}, []);

	useEffect(() => {
		if (!user) return;
		window.location.hash = "";
		window.location.search = "";

		supabase.rpc("whoami").then(({ data, error }) => {
			data = data[0];

			if (error) {
				toast.error(`Error checking admin status: ${error.message}`);
				setIsAdmin(false);
				setIsBanned(false);
			} else {
				setIsAdmin(data.is_admin);
				setIsBanned(data.is_banned);
				setBanReason(data.ban_reason);
			}
		});
	}, [user]);

	useEffect(() => {
		loadConfessions();
		fetchLastConfession();
	}, [user]);

	useEffect(() => {
		if (secondsLeft <= 0) {
			clearInterval(timerRef.current);
			return;
		}
		timerRef.current = window.setInterval(() => {
			setSecondsLeft((s) => Math.max(0, s - 1));
		}, 1000);
		return () => clearInterval(timerRef.current);
	}, [secondsLeft]);

	async function loadConfessions() {
		setLoadingConfs(true);
		const { data, error } = await supabase.rpc("get_confessions");
		if (error)
			toast.error(`Error while loading confessions: ${error.message}`);
		else setConfessions(data || []);
		setLoadingConfs(false);
	}

	async function fetchLastConfession() {
		if (!user) return;
		const { data, error } = await supabase.rpc("get_last_confession_time");
		if (error) {
			toast.error(
				`Could not fetch last confession time: ${error.message}`
			);
			return;
		}
		if (data) {
			const last = new Date(data);
			const elapsed = (Date.now() - last.getTime()) / 1000;
			const rem = Math.ceil(3600 - elapsed);
			setSecondsLeft(rem > 0 ? rem : 0);
		} else {
			setSecondsLeft(0);
		}
	}

	async function submitConfession(e: FormEvent) {
		e.preventDefault();
		if (submitDisabled) return;

		setSubmitting(true);
		const { error } = await supabase
			.from("confessions")
			.insert({ text: message.trim() });
		setSubmitting(false);

		if (error) {
			toast.error(`Could not create confession: ${error.message}`);
		} else {
			setMessage("");
			await fetchLastConfession();
			await loadConfessions();
		}
	}

	const formatTimer = (sec: number) => {
		const m = Math.floor(sec / 60)
			.toString()
			.padStart(2, "0");
		const s = (sec % 60).toString().padStart(2, "0");
		return `${m}:${s}`;
	};

	return (
		<div className="min-h-screen bg-gray-950 text-white flex flex-col items-center px-4">
			<header className="w-full max-w-2xl py-6 flex justify-between items-center border-b border-gray-800">
				<h1 className="text-2xl font-bold tracking-tight">
					Confessions
				</h1>
				{user ? (
					<button
						className="text-sm text-gray-300 hover:text-red-400 cursor-pointer"
						onClick={() => {
							supabase.auth.signOut();
							localStorage.removeItem("user");
							setUser(null);
						}}
					>
						Log out
					</button>
				) : (
					<button
						className="text-sm text-blue-400 hover:text-blue-600 cursor-pointer"
						onClick={() =>
							supabase.auth.signInWithOAuth({
								provider: "discord",
								options: {
									redirectTo: window.location.origin,
								},
							})
						}
					>
						Login with Discord
					</button>
				)}
			</header>

			<form onSubmit={submitConfession} className="w-full max-w-2xl mt-6">
				<textarea
					rows={3}
					maxLength={200}
					value={message}
					onChange={(e) => setMessage(e.target.value)}
					placeholder="Write your confession..."
					className="w-full p-3 bg-gray-900 border border-gray-700 rounded-lg"
				/>

				<div className="flex justify-end items-center mt-2">
					<button
						type="submit"
						disabled={submitDisabled}
						className={`px-4 py-2 text-sm font-medium rounded ${
							submitDisabled
								? "bg-gray-700 cursor-not-allowed"
								: "bg-blue-600 hover:bg-blue-700 cursor-pointer"
						}`}
					>
						{isBanned ? (
							`Banned for: ${banReason}`
						) : submitting ? (
							<Spinner />
						) : secondsLeft > 0 ? (
							formatTimer(secondsLeft)
						) : (
							"Submit"
						)}
					</button>
				</div>
			</form>

			<div className="w-full max-w-2xl mt-10 pb-16">
				{loadingConfs ? (
					<div className="flex justify-center mt-10">
						<Spinner />
					</div>
				) : (
					<div className="space-y-4">
						{confessions.map((c) => (
							<ConfessionCard
								key={c.id}
								id={c.id}
								text={c.text}
								created_at={c.created_at}
								isAdmin={isAdmin}
								loadConfessions={loadConfessions}
							/>
						))}
					</div>
				)}
			</div>
		</div>
	);
}
