import { supabase } from "@/db";
import { useState } from "react";
import { toast } from "react-toastify";
import Modal from "./Modal";

type ConfessionCardProps = {
	id: number;
	text: string;
	created_at: string;
	isAdmin: boolean;
	loadConfessions: () => void;
};

export default function ConfessionCard({
	id,
	text,
	created_at,
	isAdmin,
	loadConfessions,
}: ConfessionCardProps) {
	const [showDeleteDialog, setShowDeleteDialog] = useState(false);
	const [showBanDialog, setShowBanDialog] = useState(false);
	const [banReason, setBanReason] = useState("");

	async function deleteConfession() {
		const { error } = await supabase
			.from("confessions")
			.delete()
			.eq("id", id)
			.select()
			.single();

		if (error) {
			toast.error(`Failed to delete confession: ${error.message}`);
		} else {
			toast.info("Successfully deleted confession!");
			await loadConfessions();
		}
	}

	async function banAuthor() {
		const { error } = await supabase.rpc("ban_user", {
			confession_id: id,
			reason: banReason,
		});

		if (error) {
			toast.error(`Failed to ban user: ${error.message}`);
		} else {
			toast.success("User banned.");
			await loadConfessions();
		}
	}

	return (
		<div className="bg-gray-900 border border-gray-800 p-4 rounded shadow-sm relative">
			<div className="flex justify-between items-start">
				<p className="text-sm text-gray-100 break-words whitespace-pre-wrap flex-1">
					{text}
				</p>
				{isAdmin && (
					<div className="flex gap-2 text-sm ml-4 shrink-0 whitespace-nowrap">
						<button
							onClick={() => setShowDeleteDialog(true)}
							className="text-red-400 hover:underline cursor-pointer"
						>
							Delete
						</button>
						<button
							onClick={() => setShowBanDialog(true)}
							className="text-yellow-400 hover:underline cursor-pointer"
						>
							Ban
						</button>
					</div>
				)}
			</div>
			<p className="text-xs text-gray-500 mt-2">
				{new Date(created_at).toLocaleString()}
			</p>

			<Modal
				isOpen={showDeleteDialog}
				onClose={() => setShowDeleteDialog(false)}
			>
				<h2 className="text-lg font-semibold text-white">
					Confirm Delete
				</h2>
				<p className="text-sm text-gray-300 mt-2">
					Are you sure you want to delete this confession?
				</p>
				<div className="mt-6 flex justify-end gap-2">
					<button
						onClick={() => setShowDeleteDialog(false)}
						className="text-gray-300 hover:underline cursor-pointer"
					>
						Cancel
					</button>
					<button
						onClick={deleteConfession}
						className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded text-sm cursor-pointer"
					>
						Delete
					</button>
				</div>
			</Modal>
			<Modal
				isOpen={showBanDialog}
				onClose={() => setShowBanDialog(false)}
			>
				<h2 className="text-lg font-semibold text-white">Ban User</h2>
				<p className="text-sm text-gray-300 mt-2">
					Provide a reason for banning this user.
				</p>
				<div className="mt-4">
					<label className="text-sm text-gray-300 block mb-1">
						Reason
					</label>
					<input
						value={banReason}
						onChange={(e) => setBanReason(e.target.value)}
						className="w-full px-3 py-2 rounded bg-gray-800 text-white border border-gray-700 focus:outline-none"
					/>
				</div>
				<div className="mt-6 flex justify-end gap-2">
					<button
						onClick={() => setShowBanDialog(false)}
						className="text-gray-300 hover:underline cursor-pointer"
					>
						Cancel
					</button>
					<button
						onClick={banAuthor}
						className="bg-yellow-600 hover:bg-yellow-700 text-white px-4 py-2 rounded text-sm cursor-pointer"
					>
						Ban
					</button>
				</div>
			</Modal>
		</div>
	);
}
